import './index.css'
import React from 'react'
import ReactDOM from 'react-dom'
import App from './App'
import ReactGA from 'react-ga'

import { isMobile } from "react-device-detect"

import { DndProvider } from 'react-dnd'
import { HTML5Backend } from 'react-dnd-html5-backend'
import { TouchBackend } from 'react-dnd-touch-backend'

// https://levelup.gitconnected.com/using-google-analytics-with-react-3d98d709399b#:~:text=To%20begin%20integrating%20Google%20Analytics,react%2Dga%20package%20from%20npm.&text=The%20react%2Dga%20package%20is,the%20module%20in%20your%20application.

const trackingId = "UA-74643376-3"
ReactGA.initialize(trackingId)
ReactGA.set({
    // any data that is relevant to the user session
    // that you would like to track with google analytics
})

ReactDOM.render(
    <React.StrictMode>
        <DndProvider backend={isMobile ? TouchBackend : HTML5Backend}>
            <App />
        </DndProvider>
    </React.StrictMode>,
    document.getElementById('root')
)
