import { Recipe } from '../model/Recipe'
import { Workspace, WorkspaceFileJSON } from '../model/Workspace'
import {Settings} from '../model/Settings'

declare module '../model/Workspace' {
    interface Workspace {
        serialize(): Blob,
    }
}

Workspace.prototype.serialize = function() {
    let str = JSON.stringify({ fileVersion: 1, recipes: this.getRecipes(), settings: this.getSettings() });
    let bytes = new TextEncoder().encode(str);
    let blob = new Blob([bytes], {
      type: "application/json;charset=utf-8",
    }); 
    return blob;
}

export function deserializeWokspaceFile(data: string) {
    let json = JSON.parse(data);
    
    if (typeof json === "object") {
        let r: WorkspaceFileJSON = json;
        if(!r.fileVersion) r.fileVersion = 1;

        if (typeof r.fileVersion !== "undefined") {
            if (r.fileVersion !== 1) {
                throw new Error("Invalid recipe file version");
            }

            if (Array.isArray(r.recipes)) {
                return new Workspace(r.recipes.map(Recipe.fromJSON), r.settings ?? new Settings());
            }
        }
    }

    throw new Error("Invalid mealee file");
}
